@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: rgb(32, 32, 32, 1);
	background-color: var(--bg-color);
	color: rgba(219, 224, 229, 1);
	color: var(--text-color);
	font-family: 'Roboto', sans-serif;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

:root {
	--bg-color: rgb(32, 32, 32, 1);
	--text-color: rgba(219, 224, 229, 1);
	--text-soft-color: rgba(183, 188, 194, 1);
	--text-strong-color: rgba(187, 187, 187, 1);
	--subtle-color: rgba(32, 37, 42, 1);
	--border-color: rgba(36, 50, 61, 1);
	--shadow-color: rgba(0, 0, 0, 1);
	--input-color: rgba(28, 28, 28, 1);
	--outline-color: rgba(188, 140, 214, 1);
	--mark-color: rgba(175, 115, 205, 0.08);
	--special-color: rgba(175, 115, 205, 1);
	--special-bg-color: rgba(132, 74, 160, 1);
	--special-text-color: rgba(225, 222, 228, 1);
	--special-shadow-color: rgba(35, 27, 40, 1);
	--special-mark-color: rgba(225, 222, 228, 0.08);
	--light-color: rgba(224, 205, 237, 1);
	--dark-color: rgba(60, 49, 66, 1);
	--text-color-rgb: 219, 224, 229;
	--bg-color-rgb: 32, 32, 32;
	--subtle-color-rgb: 32, 37, 42;
	--special-color-rgb: 175, 115, 205;
	--special-text-color-rgb: 225, 222, 228;
	--special-bg-color-rgb: 132, 74, 160;
	--shadow-color-rgb: 0, 0, 0;
	--special-shadow-color-rgb: 35, 27, 40;
	--outline-color-rgb: 188, 140, 214;
	--dark-color-rgb: 60, 49, 66;
	--light-color-rgb: 224, 205, 237;
}

.App {
	text-align: center;
}

.content {
	margin-top: 60px;
	text-align: center;
}

.navbar {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	height: 70px;
	margin: 0;
	background-color: var(--special-bg-color);
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	list-style: none;
	-webkit-align-items: center;
	        align-items: center;
	padding-left: 20px;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.home-link {
	color: var(--text-color);
	text-decoration: none;
}

.link-group {
	width: 50%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

.navlink {
	cursor: pointer;
	border-radius: 20px;
	height: 40px;
	width: 120px;
	text-align: center;
	background-color: var(--subtle-color);
	border-style: none;
	color: var(--text-color);
	font-family: Roboto;
	font-weight: 500;
	transition: 0.3s;
}

.navlink:focus {
	outline: none;
}

.navlink:hover {
	background-color: var(--dark-color);
}

.home {
	text-align: -webkit-center;
}

.info-paragraph {
	width: 60%;
}

.post-form {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.text-entry {
	height: 70vh;
	width: 70%;
	border: none;
}

.submit-button {
	margin-top: 20px;
	background-color: var(--mark-color);
	border: 5px solid var(--special-bg-color);
	padding: 8px;
	border-radius: 8px;
	color: var(--text-color);
	cursor: pointer;
	font-weight: bold;
	transition: 0.3s;
}

.submit-button:hover {
	background-color: var(--special-bg-color);
}

.posts {
	display: grid;
	grid-template-columns: 50% 50%;
	list-style: none;
	margin: 0;
	padding: 0;
}

.category-grid {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.posts li {
	padding: 15px;
	margin: 50px;
	border-radius: 10px;
	background-color: var(--special-bg-color);
}

